import React, { Component } from 'react';
import {
  StyleSheet,
  Text,
  View,
  ScrollView,
  Linking,
  TouchableOpacity
} from 'react-native';

import DetailsComponent from "../components/DetailsComponent.js"
import EventLinks from "../components/EventLinks.js"
import WaitingComponent from "../components/WaitingComponent.js"



const styles = StyleSheet.create({
  outercontainer:{
    flex: 1,
    marginHorizontal: 15,
  },
  innercontainer:{
    justifyContent: 'center',
    alignItems: 'stretch',
  },
  desctitle: {
    fontSize: 18,
    fontWeight: 'bold',
    color: "#555",
    marginBottom: 17
  },
  baseStyle:{
    fontFamily: "Lato",
    marginLeft: 10,
    marginBottom: 30,
    fontSize: 15,
    color: "#333",
  },
  linkStyle:{
    fontFamily: "Lato",
    marginLeft: 10,
    marginBottom: 30,
    fontSize: 15,
    color: "#33F",
    textDecorationLine: 'underline'
  }
})


export default class DetailsScreen extends Component {
  constructor(props){
    super(props)
    this.state = {
      loadedevent: null,
      isFetching: true
    }
  }

  fetchData(){
    fetch("https://hangouter-backend.herokuapp.com/api/v1/events/" + this.props.match.params.id)
    .then(response => response.json())
    .then( (responseJson)=> {

      this.setState({
       event: responseJson,
       isFetching: false
      })
      })
    .catch(error=>console.log(error)) //to catch the errors if any
    }

  componentDidMount(){
    this.fetchData()
  }

  //Shamelessly copied from Stackoverflow-----------
  renderHyperlinkedText(string) {
    if (typeof string !== 'string') return null;
    var hyperlinks = this.getHyperLinkIndices(string);
    if (hyperlinks){
      const result = []
      var last = 0
      for (var i = 0; i < hyperlinks.length; i++) {
        var hyperlink = hyperlinks[i]
        var notlinkstring = string.substring(last, hyperlink.index)
        var linkstring = hyperlink.link
        var last = hyperlink.lastIndex;
        result.push(
          <Text key={2*i} style={styles.baseStyle}>
            {notlinkstring}
          </Text>
        );
        result.push(
          <TouchableOpacity onPress={() => Linking.openURL(linkstring)}>
            <Text key={2*i+1} style={styles.linkStyle}>
              {linkstring}
            </Text>
          </TouchableOpacity>
        );
      }

      if (last != string.length){
        result.push(
          <Text key={2*i+1} style={styles.baseStyle}>
            {string.substring(last, string.length)}
          </Text>
        );
      }
      return result
    } else {
      return <Text style={styles.baseStyle}>{ string }</Text>;
    }
  }

  getHyperLinkIndices(text) {
    const result = [];
    const urlRegex = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/g
    var match;
    do {
      match = urlRegex.exec(text)
      if (match) result.push({
          index: match.index,
          lastIndex: urlRegex.lastIndex,
          link: match[0]
      })
    } while(match)
    return result;
  }

  //Shamelessly copied from Stackoverflow-----------

  render() {
    // fuck this shit I'm tired of it
    // HomeScreen should pass this.props.location.state, but that is undefined
    // fuck this I hate this, why is it not here, fuckit
    // TODO because of that, site will not use cached data
    if (this.state.isFetching){
    return (
      <WaitingComponent/>
    )
    }else{
      var item = this.state.event
      return (
        <View style={styles.outercontainer}>
          <ScrollView>
            <View style={styles.innercontainer}>
              <DetailsComponent item={item}/>
            </View>
            <View>
              {this.renderHyperlinkedText(item.description)}
            </View>
            <EventLinks links={item.links}/>
          </ScrollView>
        </View>)
    }
      /*
      <View style={styles.footercontainer}>
        <Image source={{uri: 'https://image.flaticon.com/icons/png/512/87/87390.png'}}
        style={styles.logoimage}/>
        <Image source={{uri: 'https://image.flaticon.com/icons/png/512/33/33702.png'}}
        style={styles.logoimage}/>
        <Image source={{uri: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTEfRvC9JARU1UHdLD6Xg9BexK99nVG9dKjwfhmNDsp9anQLEVF'}}
        style={styles.logoimage}/>
      </View>
      </View>*/
  }
}
